{
    return (
      <div style={{width:'100%', alignItems: 'center', display: 'flex'}}>
            <div style={{color:'#6c7890'}}>Project Management</div>
            <ChevronRight size={14} color="white" style={{margin:'0px 10px'}} />
            <div style={{color:'white'}}>{ name }</div>
      </div>
    );
}

