function ProjectCreate()
{
    // 프로젝트 메인 페이지로부터 전달 받은 파라미터 state
    const {state} = useLocation();

    const [project_id, setProject_id] = useState('');                                   // 프로젝트 ID
    const [project_name, setProject_name] = useState('');                               // 프로젝트 이름

    // 현재 페이지 정보가 변경될 경우 반복 호출
    useEffect(() =>
    {
        if(  !!isToken() === false || !!isUser() === false )
        {
            /* 로그인 상태가 아닌 경우  */
            window.location.replace("/");
        }

        /* 전달 받은 파라미터가 있는 경우 */
        if(state === null)
        {
            alert('비정상적인 접근입니다.')
            window.location.replace("/");
        }
        else
        {
            setProject_id(state.id);
            setProject_name(state.name);
        }
    }, []);


    return (
        <div className='manage_container' style={{backgroundColor: '#191c22'}} >
            {/* 프로젝트 생성 페이지 - 헤더 */}
            <div className='manage_header' style={{width:'100%'}}>
                <div className='path'>
                   {/* 현재 페이지 이동 경로 */}
                   {/* { Breadcrumb(project_name) } */}
                   { Breadcrumb('Project Create') }
                </div>

                <div className='title'>
                    <div className='title_left'>{ project_name }</div>

                    <div className='title_right'></div>
                </div>
            </div>

            <div className='manage_bottom'>
                <div className='manage_bottom_menu' >

                    {/* 페이지 이동 - BreadCrumb */}
                    <div className="PMgmtRouter-item">
                        <div className="PMgmtRouter-select" style={{minWidth:'59px'}}>
                            신경망 생성 및 탑재<div></div>
                        </div>
                    </div>
                </div>

                <div className='manage_bottom_component' style={{backgroundColor:'white'}}>
                    {/* 신경망 생성 페이지 */}
                    <NeuralAndLoadPage/>
                </div>
            </div>
        </div>
    );
}

export default ProjectCreate;